@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');

:root {
    --primary-color: #4a90e2;
    --background-color: #f0f2f5;
    --sent-bg-color: var(--primary-color);
    --received-bg-color: #ffffff;
    --text-color-light: #ffffff;
    --text-color-dark: #333;
    --notification-text-color: #888;
    --header-bg-color: #ffffff;
    --footer-bg-color: #f0f2f5;
    --input-bg-color: #ffffff;
    --border-color: #e0e0e0;
    --box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
}

.chat-container {
    display: flex;
    flex-direction: column;
    height: 100vh;
    font-family: 'Poppins', sans-serif;
    transition: background-color 0.3s;
}

.chat-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 25px;
    background-color: var(--header-bg-color);
    box-shadow: var(--box-shadow);
    z-index: 10;
}

.chat-header h2 {
    margin: 0;
    font-size: 20px;
    font-weight: 600;
    color: var(--text-color-dark);
}

.chat-header p {
    margin: 0;
    color: #666;
}

.btn-leave-room {
    background-color: #f44336;
    color: white;
    border: none;
    padding: 8px 15px;
    border-radius: 8px;
    cursor: pointer;
    font-size: 14px;
    font-weight: 600;
    transition: background-color 0.3s ease;
}

.btn-leave-room:hover {
    background-color: #d32f2f;
}

.chat-messages {
    flex: 1;
    overflow-y: auto;
    padding: 20px;
    display: flex;
    flex-direction: column;
}

.message-item {
    display: flex;
    margin-bottom: 4px;
    max-width: 75%;
    align-items: flex-end;
}

.message-item.sent {
    align-self: flex-end;
    flex-direction: row-reverse;
}

.message-item.received {
    align-self: flex-start;
}

.message-item.notification {
    align-self: center;
    color: var(--notification-text-color);
    font-size: 12px;
    margin: 10px 0;
    max-width: 100%;
}

.user-avatar {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    background-color: #d0e0f0;
    color: var(--primary-color);
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    margin-right: 10px;
    flex-shrink: 0;
}

.message-content-wrapper {
    display: flex;
    flex-direction: column;
}

.message-item.sent .message-content-wrapper {
    align-items: flex-end;
}

.message-item.received .message-content-wrapper {
    align-items: flex-start;
}

.message-bubble {
    padding: 10px 15px;
    border-radius: 18px;
    position: relative;
}

.message-item.sent .message-bubble {
    background-color: var(--sent-bg-color);
    color: var(--text-color-light);
    border-bottom-right-radius: 4px;
}

.message-item.received .message-bubble {
    background-color: var(--received-bg-color);
    color: var(--text-color-dark);
    border-bottom-left-radius: 4px;
    box-shadow: 0 1px 2px rgba(0,0,0,0.05);
}

.message-item.consecutive .message-bubble {
    margin-top: 2px;
}

.message-text {
    margin: 0;
    word-wrap: break-word;
}

.message-text a {
    color: inherit;
    text-decoration: underline;
}

.message-text .file-link {
    font-weight: 600;
}

.message-timestamp {
    font-size: 11px;
    color: #999;
    margin-top: 5px;
    padding: 0 5px;
}

.chat-footer {
    padding: 15px 25px;
    background-color: var(--footer-bg-color);
    border-top: 1px solid var(--border-color);
}

.send-message-form {
    display: flex;
    align-items: center;
    gap: 10px;
}

.message-input {
    flex: 1;
    padding: 12px 15px;
    border: 1px solid var(--border-color);
    border-radius: 22px;
    background-color: var(--input-bg-color);
    font-size: 16px;
    transition: border-color 0.3s, box-shadow 0.3s;
}

.message-input:focus {
    outline: none;
    border-color: var(--primary-color);
    box-shadow: 0 0 0 2px rgba(74, 144, 226, 0.2);
}

.settings-button,
.upload-button,
.send-button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    border: none;
    border-radius: 50%;
    background-color: transparent;
    color: #555;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
}

.settings-button:hover,
.upload-button:hover {
    background-color: #e0e0e0;
}

.send-button {
    background-color: var(--primary-color);
    color: white;
    padding: 12px;
}

.send-button:hover {
    background-color: #357abd;
}
